import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { ConsultaService } from '../../services/consulta.service';
@Component({
  selector: 'bingazo-boletin',
  templateUrl: './boletin.component.html',
  styleUrls: ['./boletin.component.scss'],
})
export class BoletinComponent implements OnInit {
  boletin: any;
  boletines: any;
  sorteosJugados: any;
  sorteoGanador: any;
  imgNotFound: boolean = false;
  showBox: boolean = false;
  sorteo: any;
  constructor(
    private actRoute: ActivatedRoute,
  ) {

    this.actRoute.params.subscribe((params) => {
      this.sorteoGanador! = params['sorteo'];
    });
  }

  async ngOnInit() {
    this.triggerLoader();
    this.sorteosJugados = JSON.parse(localStorage.getItem('bingazoSorteos')!);
    this.boletines = JSON.parse(localStorage.getItem('bingazoBoletines')!);
    /*     this.sorteoGanador = this.sorteosJugados[0];
        this.boletin = this.boletines[0].ruta;
     */
    this.changeBoletin();
    this.showBox = true;
    this.dismissLoader();
  }

  changeBoletin() {
    this.showBox = false;
    this.boletin = "";
    let i = this.boletines.findIndex(
      (o: any) => o.sorteo == this.sorteoGanador
    );
    this.boletin = this.boletines[i].ruta;
    this.showBox = true;
  }

  handleImgError() {
    this.imgNotFound = true;
  }

  isLoading: boolean = false;
  loadingMessage: String = 'Buscando el boletin';

  triggerLoader() {
    this.isLoading = true;
  }

  dismissLoader() {
    this.isLoading = false;
  }
}
