import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { PageEvent } from '@angular/material/paginator';
import {
  sorteo,
  ticketsNacional,
  ticketsAnimales,
  ticketsLotto,
  animales,
} from '../../interfaces/venta';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';

import { VentaService as PozoService } from '../../../juegos/pozo/services/venta.service';
import { VentaService as PozoRevanchaService } from '../../../juegos/pozoRevancha/services/venta.service';
import { VentasService } from '../../services/ventas.service';
import { PagosService } from '../..//services/pagos.service';
import { CarritoService } from '../../services/carrito.service';
@Component({
  selector: 'app-pozo',
  templateUrl: './pozo.component.html',
  styleUrls: ['./pozo.component.scss'],
})
export class PozoComponent implements OnInit {
  combinacionDeLaSuerte: string[] = ['', '', '', ''];

  animalesTabs: animales[] = [];
  revanchas?: boolean[];
  @ViewChild('figureSelector') figureSelector: any;
  buscarCartones: boolean = true;

  page_size: number = 12;
  page_number: number = 1;
  showNumeros: boolean = false;
  sorteo?: sorteo[];
  seleccionAnimales?: animales[];
  ticketsDisponibles?: any;
  ticketsDisponiblesRevancha?: any;
  pageSizeOptions?: [5, 10, 20, 100];
  token?: string;
  usuario?: string;
  ticketsPozoRevancha: any = {};
  ticketsPozo: any = {};

  codigoPromocional: any = [];
  constructor(
    private ventas: VentasService,
    private actRoute: ActivatedRoute,
    private paymentService: PagosService,
    private cart: CarritoService,
    private pozo: PozoService,
    private pozoRevancha: PozoRevanchaService,
    private router: Router,
    private changeDetectorRef: ChangeDetectorRef
  ) {
    this.actRoute.params.subscribe((params) => {
      this.token = params['token'];
    });
  }
  total?: string;
  getTotal() {
    this.changeDetectorRef.detectChanges();

    this.total = this.cart.getTotal();
    this.changeDetectorRef.markForCheck();
  }

  openSelector() {
    this.figureSelector.open();
    this.buscarCartones = true;
  }
  agregar(animal: animales, i: number) {
    if (this.seleccionAnimales![i].status === false) {
      this.seleccionAnimales![i].status = true;
      this.animalesTabs.push(this.seleccionAnimales![i]);
    } else {
      this.seleccionAnimales![i].status = false;
      this.animalesTabs = this.animalesTabs.filter((element) => {
        return element.nombre !== animal.nombre;
      });
    }
    localStorage.setItem(
      'animalesSeleccionados',
      JSON.stringify(this.seleccionAnimales)
    );
    localStorage.setItem('animalesTabs', JSON.stringify(this.animalesTabs));
  }

  async agregarRevancha(id: number) {
    try {
      this.isLoading = true;
      let count = (await this.cart.getCount()) + 2;
      if (count <= 1000) {
        this.isLoading = false;
        this.ticketsDisponibles![id].status = true;
        await this.pushToRevancha(id);
      } else {
        this.changeDetectorRef.detectChanges();
        this.ticketsDisponiblesRevancha![id].status = false;
        this.changeDetectorRef.markForCheck();
        let errorMessage =
          'Incluir el boleto excede el límite de compra. Si quieres escoger este boleto, por favor elimina algún otro de tu carrito.';
        this.openError(errorMessage);
        this.isLoading = false;
      }
      return;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  async pushToRevancha(id: any) {
    try {
      this.loadingMessage = 'Agregando boleto al carrito';
      this.isLoading = true;
      let subtotal =
        parseFloat(this.sorteoSeleccionado!.precio) +
        parseFloat(this.sorteoSeleccionado!.sorteoRevancha.precio);
      let auxPozo = {
        ticket: this.ticketsDisponibles[id],
        sorteo: this.sorteoSeleccionado,
        subtotal: this.sorteoSeleccionado!.precio,
      };
      let auxRevancha = {
        ticket: this.ticketsDisponiblesRevancha[id],
        sorteo: this.sorteoSeleccionado!.sorteoRevancha,
        subtotal: this.sorteoSeleccionado!.sorteoRevancha.precio,
      };
      let hasBalance = await this.paymentService.hasBalance(
        subtotal,
        this.token
      );

      if (hasBalance) {
        this.ticketsPozo[this.ticketsDisponibles[id].identificador] = auxPozo;
        this.ticketsPozoRevancha[
          this.ticketsDisponiblesRevancha[id].identificador
        ] = auxRevancha;
        let reservaId = this.ventas.getReservaId();
        let response = await this.ventas.reservarRevancha(
          auxPozo,
          auxRevancha,
          reservaId
        );

        this.ventas.setReservaId(response);
        await this.cart.setCarrito(auxPozo, 5);
        await this.cart.setCarrito(auxRevancha, 17);
        await this.cart.setCarritoPozo(this.ticketsPozo);
        await this.cart.setCarritoPozoRevancha(this.ticketsPozoRevancha);
        await this.getCarritoTickets();
        //this.getTotal();
        this.isLoading = false;
      } else {
        this.isLoading = false;
        let message =
          'Tu saldo es insuficiente para agregar este boleto al carrito';
        this.ticketsDisponibles!.find(
          (x: any) =>
            x.identificador === this.ticketsDisponibles[id].identificador
        ).status = false;
        this.ticketsDisponiblesRevancha!.find(
          (x: any) =>
            x.identificador ===
            this.ticketsDisponiblesRevancha[id].identificador
        ).status = false;
        this.recargarSaldo(message);
      }
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }
  async seleccionarTicket(id: any) {
    try {
      this.isLoading = true;
      this.changeDetectorRef.detectChanges();
      this.ticketsDisponibles![id].status =
        !this.ticketsDisponibles![id].status;
      this.changeDetectorRef.markForCheck();
      if (!this.ticketsDisponibles![id].status) {
        let identificador = this.ticketsDisponibles![id].identificador;
        let ticketPozo = this.ticketsPozo[identificador];
        this.isLoading = false;
        await this.deletePozoTicket(ticketPozo);
      } else {
        let count = (await this.cart.getCount()) + 1;
        if (count <= 1000) {
          this.isLoading = false;

          this.ticketsDisponibles![id].status = true;
          await this.pushToSeleccionado(this.ticketsDisponibles![id], 5);
        } else {
          this.changeDetectorRef.detectChanges();
          this.ticketsDisponibles![id].status = false;
          this.changeDetectorRef.markForCheck();
          let errorMessage =
            'Incluir el boleto excede el límite de compra. Si quieres escoger este boleto, por favor elimina algún otro de tu carrito.';
          this.openError(errorMessage);
          this.isLoading = false;
        }
      }
      await this.setDescuento(5);
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  async pushToSeleccionado(ticket: any, tipoLoteria: number) {
    try {
      this.loadingMessage = 'Agregando boleto al carrito';
      this.isLoading = true;
      let subtotal = this.sorteoSeleccionado!.precio;
      let aux = {
        ticket,
        sorteo: this.sorteoSeleccionado,
        subtotal,
      };
      let hasBalance = await this.paymentService.hasBalance(
        subtotal,
        this.token
      );

      if (hasBalance) {

        this.ticketsPozo[ticket.identificador] = aux;
        let reservaId = this.ventas.getReservaId();
        let response = await this.ventas.reservarBoletos(
          this.token!,
          aux,
          tipoLoteria,
          reservaId
        );

        this.ventas.setReservaId(response);
        await this.cart.setCarrito(aux, tipoLoteria);
        await this.cart.setCarritoPozo(this.ticketsPozo);
        await this.getCarritoTickets();
        //this.getTotal();
        this.isLoading = false;
      } else {
        this.isLoading = false;
        let message =
          'Tu saldo es insuficiente para agregar este boleto al carrito';
        this.ticketsDisponibles!.find(
          (x: any) => x.identificador === ticket.identificador
        ).status = false;
        this.recargarSaldo(message);
      }
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  handlerPage(e: PageEvent) {
    this.page_size = e.pageSize;
    this.page_number = e.pageIndex + 1;
  }

  obtenerMascota(mascota: any) {
    return this.pozo.obtenerMascota(mascota);
  }

  over25Error: boolean = false;
  over25ErrorTag: string =
    'Los números no pueden ser mayores a 25. Por favor, toma esto en cuenta al ingresar tus números preferidos.';

  validate() {
    let isHigher = false;
    this.combinacionDeLaSuerte.forEach((number, i) => {
      let aux = parseInt(number);
      if (aux > 25) {
        this.combinacionDeLaSuerte[i] = '';
        this.over25Error = true;
        setTimeout(() => {
          this.over25Error = false;
        }, 6000);
      }
    });
  }
  async buscarNumero() {
    try {
      this.loadingMessage = 'Buscando combinaciones disponibles';
      this.isLoading = true;
      this.showNumeros = false;
      let combinacion = this.combinacionDeLaSuerte.map((number) => {
        let numero = number;
        if (numero.length < 2) {
          numero = `0${numero}`;
        }

        if (numero == '0' || numero == '00') {
          return '';
        }
        return numero;
      });
      let combinacionFigura = this.animalesTabs.map((animal) => {
        return animal.identificador;
      });
      combinacion.sort(this.ordenaCombinacion);
      combinacionFigura.sort(this.ordenaCombinacion);
      let authData = this.ventas.getAuthData();
      this.ticketsDisponibles = await this.pozo.obtenerTickets(
        this.sorteoSeleccionado!.sorteo,
        combinacion.join(''),
        combinacionFigura.join(''),
        this.tipoSeleccion,
        authData
      );
      this.ticketsDisponiblesRevancha = JSON.parse(
        JSON.stringify(this.ticketsDisponibles)
      );
      this.ticketsDisponiblesRevancha = this.ticketsDisponiblesRevancha.map(
        (ticket: any) => {
          ticket.identificador += 1;
          return ticket;
        }
      );
      this.combinacionDeLaSuerte = ['', '', '', ''];
      this.showNumeros = true;
      this.figureSelector.close();
      this.buscarCartones = false;
      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  ordenaCombinacion(a: any, b: any) {
    return a - b;
  }
  tipoSeleccion: number = 96;
  boleto: string = "";
  boletoRevancha: string = "";
  sorteoSeleccionado?: sorteo;
  procesaEmitir(sorteo: any) {
    this.sorteoSeleccionado = sorteo;

    this.boleto = this.ventas.obtenerImagenBoleto(
      5,
      this.sorteoSeleccionado?.sorteo
    );
    if (this.sorteoSeleccionado?.tieneRevancha) {

      this.boletoRevancha = this.ventas.obtenerImagenBoleto(
        17,
        this.sorteoSeleccionado?.sorteoRevancha.sorteo
      );
    }
    this.openSelector();

    this.showNumeros = false;
  }
  isLoading?: boolean;
  showComponents: boolean = false;
  loadingMessage?: string;

  obtenerAnimal(mascota: any) {
    return this.pozo.obtenerMascota(mascota);
  }
  saldoInsuficiente: boolean = false;


  comprar() {
    this.router.navigateByUrl(`/compra_tus_juegos/confirmar_venta/${this.token!}`);
  }
  irARecarga() { }

  recargaDeSaldoMessage?: string;
  recargarSaldo(message: any) {
    this.recargaDeSaldoMessage = message;
    this.saldoInsuficiente = true;
  }
  ticketsLoteria: any = {};
  ticketsLotto: any = {};
  descuentos: any;
  async ngOnInit() {
    try {
      this.isLoading = true;
      await this.getCarritoTickets();
      //this.getTotal();

      this.loadingMessage = 'Cargando los sorteos disponibles';
      this.seleccionAnimales = JSON.parse(
        localStorage.getItem('animalesSeleccionados')!
      );
      this.animalesTabs = JSON.parse(localStorage.getItem('animalesTabs')!);

      //TODO: Preguntar como quiere que venga la variable tabs, si llena o no
      this.seleccionAnimales!.forEach((element) => {
        this.animalesTabs.forEach((elemento) => {
          if (elemento.nombre === element.nombre) {
            element.status = elemento.status;
          }
        });
      });
      localStorage.setItem(
        'animalesSeleccionados',
        JSON.stringify(this.seleccionAnimales)
      );
      let authData = this.ventas.getAuthData();
      this.sorteo = await this.pozo.obtenerSorteo(authData);

      let sorteosRevancha = await this.pozoRevancha.obtenerSorteo(authData);
      this.sorteo = this.sorteo.map((item) => {
        if (item.tieneRevancha) {
          let index = sorteosRevancha.findIndex(
            (sorteo) => sorteo.sorteo == item.sorteoRevancha
          );
          item.sorteoRevancha = sorteosRevancha[index];
        }
        return item;
      });
      this.descuentos = await this.ventas.obtenerDescuentos();
      this.isLoading = false;
      this.showComponents = true;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  async setDescuento(tipoLoteria: any) {
    return;
    let descuentos = this.descuentos.filter(
      (element: any) => parseInt(element.tipoLoteria) == tipoLoteria
    );
    for (let index = 0; index < descuentos.length; index++) {
      const element = descuentos[index];
      let conteo = await this.cart.contarBoletos(element.sorteo, tipoLoteria);
      if (conteo >= parseInt(element.cantidad)) {
        await this.cart.calcularDescuento(element);
      } else {
        await this.cart.eliminarDescuento(element, tipoLoteria);
      }
      await this.getCarritoTickets();
    }
    await this.cart.setTotalConDesc();
  }
  async deleteLoteriaTicket(identificador: any) {
    try {
      let data = this.ticketsLoteria[identificador]
      let fracciones = data.ticket.seleccionados;
      this.loadingMessage = 'Removiendo boleto del carrito';
      this.isLoading = true;
      let ticket = this.ticketsLoteria[identificador].ticket;
      let sorteo = data.sorteo;

      let reservaId = this.ventas.getReservaId();
      if (fracciones.length != 0) {
        let response = await this.ventas.eliminarBoletosDeReserva(
          this.token,
          ticket,
          sorteo,
          fracciones,
          1,
          reservaId
        );
      }
      delete this.ticketsLoteria[identificador];

      await this.cart.removeFromCart(ticket, 5);
      await this.cart.setCarritoLoteria(this.ticketsLoteria);

      await this.getCarritoTickets();
      //this.getTotal();
      await this.setDescuento(1);

      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }
  async deleteLottoTicket(identificador: any) {
    try {
      let data = this.ticketsLotto[identificador]
      let fraccion = '';
      this.loadingMessage = 'Removiendo boleto del carrito';
      this.isLoading = true;
      let ticket = this.ticketsLotto[identificador].ticket;
      let sorteo = data.sorteo;

      let reservaId = this.ventas.getReservaId();
      let response = await this.ventas.eliminarBoletosDeReserva(
        this.token,
        ticket,
        sorteo,
        fraccion,
        2,
        reservaId
      );

      delete this.ticketsLotto[identificador];

      await this.cart.removeFromCart(ticket, 2);
      await this.cart.setCarritoLotto(this.ticketsLotto);
      await this.getCarritoTickets();
      //this.getTotal();
      await this.setDescuento(2);
      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  async deleteLoteriaFraccion(data: any) {
    try {
      this.loadingMessage = 'Removiendo boleto del carrito';
      this.isLoading = true;
      let identificador = data.ticket.ticket.identificador;
      let ticket = data.ticket.ticket;
      let sorteo = data.ticket.sorteo;
      let fraccion = data.fraccion;

      let reservaId = this.cart.getReservaId();
      let response = await this.ventas.eliminarBoletosDeReserva(
        this.token,
        ticket,
        sorteo,
        [fraccion],
        1,
        reservaId
      );
      let index = this.ticketsLoteria[
        identificador
      ].ticket.seleccionados.findIndex((x: any) => x == fraccion);
      this.ticketsLoteria[identificador].ticket.seleccionados.splice(index, 1);
      if (this.ticketsLoteria[identificador].ticket.seleccionados.length == 0) {
        delete this.ticketsLoteria[identificador];
      }

      await this.cart.removeFromCart(ticket, 1);
      await this.cart.setCarritoLoteria(this.ticketsLoteria);
      await this.getCarritoTickets();
      //this.getTotal();
      await this.setDescuento(1);
      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }
  async deletePozoTicket(identificador: any) {
    try {
      this.loadingMessage = 'Removiendo boleto del carrito';
      let data = this.ticketsPozo[identificador];

      let fraccion = '';
      let ticket = this.ticketsPozo[identificador].ticket;
      let sorteo = this.ticketsPozo[identificador].sorteo;

      if (this.ticketsPozoRevancha[identificador + 1]) {
        await this.deletePozoRevanchaTicket(
          identificador + 1
        );
      }
      this.isLoading = true;
      let reservaId = this.ventas.getReservaId();
      let response = await this.ventas.eliminarBoletosDeReserva(
        this.token,
        ticket,
        sorteo,
        fraccion,
        5,
        reservaId
      );

      delete this.ticketsPozo[identificador];

      await this.cart.removeFromCart(ticket, 5);
      await this.cart.setCarritoPozo(this.ticketsPozo);
      if (this.ticketsDisponibles && this.ticketsDisponibles.length) {
        let deletedIndex = this.ticketsDisponibles.findIndex(
          (x: any) => x.identificador === identificador
        );
        if (deletedIndex != -1)
          this.ticketsDisponibles[deletedIndex].status = false;
      }
      await this.getCarritoTickets();
      //this.getTotal();
      await this.setDescuento(5);

      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  ticketsBingazo: any = {}
  async deleteBingazoTicket(identificador: any) {
    try {
      let data = this.ticketsBingazo[identificador];
      let fraccion = '';
      this.loadingMessage = 'Removiendo boleto del carrito';
      this.isLoading = true;
      let ticket = this.ticketsBingazo[identificador].ticket;
      let sorteo = data.sorteo;

      let reservaId = this.cart.getReservaId();
      let response = await this.ventas.eliminarBoletosDeReserva(
        this.token,
        ticket,
        sorteo,
        fraccion,
        12,
        reservaId
      );

      delete this.ticketsBingazo[identificador];

      await this.cart.setCarritoBingazo(this.ticketsBingazo);

      await this.getCarritoTickets();
      this.getTotal();
      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      let errorTitle = 'Error';
      this.openError(errorMessage);
    }
  }
  async deletePozoRevanchaTicket(identificador: any) {
    try {
      this.loadingMessage = 'Removiendo boleto del carrito';
      this.isLoading = true;
      let data = this.ticketsPozoRevancha[identificador];
      let fraccion = '';
      let ticket = this.ticketsPozoRevancha[identificador].ticket;
      let sorteo = this.ticketsPozoRevancha[identificador].sorteo;
      let reservaId = this.ventas.getReservaId();
      let response = await this.ventas.eliminarBoletosDeReserva(
        this.token,
        ticket,
        sorteo,
        fraccion,
        17,
        reservaId
      );

      delete this.ticketsPozoRevancha[identificador];

      await this.cart.removeFromCart(ticket, 17);
      await this.cart.setCarritoPozoRevancha(this.ticketsPozoRevancha);
      if (
        this.ticketsDisponiblesRevancha &&
        this.ticketsDisponiblesRevancha.length
      ) {
        let deletedIndex = this.ticketsDisponiblesRevancha.findIndex(
          (x: any) => x.identificador === identificador
        );
        if (deletedIndex != -1)
          this.ticketsDisponiblesRevancha[deletedIndex].status = false;
      }
      await this.getCarritoTickets();
      //this.getTotal();
      await this.setDescuento(17);
      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }
  async deleteAllTickets() {
    try {
      this.loadingMessage = 'Removiendo boletos del carrito';
      this.isLoading = true;

      Object.keys(this.ticketsPozo).forEach((key) => {
        if (this.ticketsDisponibles && this.ticketsDisponibles.length != 0) {
          let deletedIndex = this.ticketsDisponibles.findIndex(
            (x: any) => x.identificador == key
          );
          if (deletedIndex != -1)
            this.ticketsDisponibles[deletedIndex].status = false;
        }
      });
      Object.keys(this.ticketsPozoRevancha).forEach((key) => {
        if (
          this.ticketsDisponiblesRevancha &&
          this.ticketsDisponiblesRevancha.length != 0
        ) {
          let deletedIndex = this.ticketsDisponiblesRevancha.findIndex(
            (x: any) => x.identificador == key
          );
          if (deletedIndex != -1)
            this.ticketsDisponiblesRevancha[deletedIndex].status = false;
        }
      });
      await this.cart.borrarCarrito();
      await this.getCarritoTickets();
      //this.getTotal();
      this.isLoading = false;
    } catch (e: any) {
      this.isLoading = false;
      console.log(e.message);
      let errorMessage = e.message;
      this.openError(errorMessage);
    }
  }

  async getCarritoTickets() {
    let carrito = await this.cart.buscarCarrito();
    this.ticketsLoteria = carrito.loteria;
    this.ticketsLotto = carrito.lotto;
    this.ticketsBingazo = carrito.bingazo;
    this.ticketsPozo = carrito.pozo;
    this.ticketsPozoRevancha = carrito.pozoRevancha;
  }
  isError: boolean = false;
  errorMessage?: string;
  openError(msg: any) {
    this.errorMessage = msg;
    this.isError = true;
  }

  closeError() {
    this.isError = false;
  }

  isValidationError: boolean = false;
  validationErrorMessage?: string;
  openValidationError(msg: any) {
    this.validationErrorMessage = msg;
    this.isValidationError = true;
  }

  closeValidationError() {
    this.isValidationError = false;
    this.validationErrorMessage = '';
    window.location.reload();
  }
}
